"use client";
import { useForm } from "react-hook-form";
import FormInput from "./Form";
import Form from './Form';
import FormSelect from './FormSelect';
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation"
import { addEquipment } from "@/redux/reducers/equipmentReducer";
import { useDispatch, useSelector } from 'react-redux';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import FormCheckbox from './FormCheckbox';
import { Loader2 } from 'lucide-react';
import { Plus, X } from 'lucide-react';
import ListingSummary from '@/components/listing/Summary'

export default function EquipmentForm  ()  {
    const [step, setStep] = useState(1);
    // const [loading, setLoading] = useState(false);
    const [accessories, setAccessories] = useState([]);
    const [inputValue, setInputValue] = useState('');
    const dispatch = useDispatch();
    const router = useRouter();
    const [images, setImages] = useState([]);
    const [video, setVideo] = useState(null);
    const [previewImages, setPreviewImages] = useState([]);
    const [mediaFiles, setMediaFiles] = useState({
      images: [],
      videos: []
    });
  
    const { loading } = useSelector((state) => state.equipment || {});
  

    const {
      register,
      handleSubmit,
      formState: { errors },
      watch 
    } = useForm();

    useEffect(() => {
      const token = localStorage.getItem('token');

      if (!token) {
      
        router.push('/login');
        toast.error('Please log in to add equipment');
      }
    }, [router]);
  
    const handleImageUpload = (e) => {
      const files = Array.from(e.target.files);
    
    
    const validFiles = files.filter(file => {
      const isValid = file.type.startsWith('image/');
      const isValidSize = file.size <= 5 * 1024 * 1024; 
      
      if (!isValid) toast.error('Please upload only image files');
      if (!isValidSize) toast.error('Image size should be less than 5MB');
      
      return isValid && isValidSize;
    });

    if (validFiles.length === 0) return;

    setImages(validFiles);
      
      const previews = files.map(file => URL.createObjectURL(file));
      setPreviewImages(previews);
    };
  
    const handleVideoUpload = (e) => {
      const file = e.target.files[0];
    
    if (file) {
      const isValid = file.type.startsWith('video/');
      const isValidSize = file.size <= 50 * 1024 * 1024; 
      if (!isValid) {
        toast.error('Please upload a valid video file');
        return;
      }
      if (!isValidSize) {
        toast.error('Video size should be less than 50MB');
        return;
      }
      
      setVideo(file);
    }
  };
  
  

  const onSubmit = async (data) => {
    try {
      console.log('Final Form Data:', {
        // Basic Details
        location: data.location,
        category: data.category,
        subcategory: data.subcategory,
        name: data.name,
        description: data.description,
  
        // Media Files
        mediaFiles: {
          images: mediaFiles.images,
          videos: mediaFiles.videos
        },
  
        // Equipment Details
        brand: data.brand,
        model: data.model,
        workingcondition: data.workingcondition,
  
        // Pricing & Payment
        pricingschedule: data.pricingschedule,
        amount: data.amount,
        negotiable: data.negotiable,
        paymentMethod: data.paymentMethod,
  
        // Owner Details
        owner: {
          name: data.ownerName,
          city: data.ownerCity,
          phonenumber: data.ownerPhone,
          email: data.ownerEmail
        }
      });

      const formData = new FormData();
  
      const userId = "6798e8a28eacebb41f667db3";
      formData.append('userId', userId); 
      formData.append('location', data.location);
      formData.append('category', data.category);
      formData.append('subcategory', data.subcategory);
      formData.append('name', data.name);
      formData.append('description', data.description);
      formData.append('brand', data.brand);
      formData.append('model', data.model);
      
  
      formData.append('workingcondition', data.workingcondition === 'new');
      formData.append('negotiable', data.negotiable === true);
    
      formData.append('pricingschedule', data.pricingschedule);
      formData.append('amount', data.amount);
  
      formData.append('owner[name]', data.ownerName);
      formData.append('owner[city]', data.ownerCity);
      formData.append('owner[phonenumber]', data.ownerPhone);
      formData.append('owner[email]', data.ownerEmail);
  
      
      if (coverImage instanceof File) {
        formData.append('coverimage', coverImage);
      }
  
      if (mediaFiles.images && mediaFiles.images.length > 0) {
        mediaFiles.images.forEach(image => {
          if (image instanceof File) {
            formData.append('thumbnails', image);
          }
        });
      }
  
      if (video instanceof File) {
        formData.append('video', video);
      }
  
      if (document instanceof File) {
        formData.append('document', document);
      }
  
      const resultAction = await dispatch(addEquipment(formData));
  
      if (resultAction.type === addEquipment.fulfilled.type) {
        toast.success(resultAction.payload.message);
        router.push('/');
      } else {
        const errorMessage = resultAction.payload?.error || 
                            resultAction.payload?.message || 
                            'Failed to add equipment';
        toast.error(errorMessage);
      }
    } catch (error) {
      console.error('Equipment creation error:', error);
      toast.error('Failed to create equipment listing');
    }
  };


  const renderStep1 = () => {
  
  const handleImageUpload = (event) => {
    const files = Array.from(event.target.files);
    
    files.forEach(file => {
      const reader = new FileReader();
      reader.onloadend = () => {
        setMediaFiles(prev => ({
          ...prev,
          images: [...prev.images, {
            id: Date.now(),
            file: file,
            preview: reader.result
          }]
        }));
      };
      reader.readAsDataURL(file);
    });
  };

  const handleVideoUpload = (event) => {
    const files = Array.from(event.target.files);
    
    files.forEach(file => {
      const reader = new FileReader();
      reader.onloadend = () => {
        setMediaFiles(prev => ({
          ...prev,
          videos: [...prev.videos, {
            id: Date.now(),
            file: file,
            preview: reader.result
          }]
        }));
      };
      reader.readAsDataURL(file);
    });
  };

  const removeMedia = (type, id) => {
    setMediaFiles(prev => ({
      ...prev,
      [type]: prev[type].filter(media => media.id !== id)
    }));
  };

  return (
    <div className="w-full max-w-2xl mx-auto p-4 sm:p-6">
      <ToastContainer />
      <h2 className="text-lg sm:text-xl font-semibold mb-4 sm:mb-6">Basic Details</h2>
      
      <div className="space-y-4 sm:space-y-6">
        <FormInput
          label="Location"
          name="location"
          register={register}
          error={errors.location}
        />

        <FormSelect
          label="Category"
          name="category"
          register={register}
          options={[
            { value: 'electronics', label: 'Electronics' },
            { value: 'machinery', label: 'Machinery' },
            { value: 'construction', label: 'Construction' },
            { value: 'agriculture', label: 'Agriculture' }
          ]}
          error={errors.category}
        />

        <FormSelect
          label="Sub Category"
          name="subcategory"
          register={register}
          options={[
            { value: 'heavy', label: 'Heavy Equipment' },
            { value: 'light', label: 'Light Equipment' },
            { value: 'tools', label: 'Tools' },
            { value: 'accessories', label: 'Accessories' }
          ]}
          error={errors.subcategory}
        />

        <FormInput
          label="Equipment Name"
          name="name"
          register={register}
          error={errors.name}
        />

        <FormInput
          label="Description"
          name="description"
          register={register}
          error={errors.description}
        />

     
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">Images</label>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-2 sm:gap-4">
           
            <label className="aspect-square bg-yellow-50 rounded-lg flex items-center justify-center cursor-pointer hover:bg-yellow-100 transition-colors">
              <input
                type="file"
                accept="image/*"
                onChange={handleImageUpload}
                className="hidden"
                multiple
              />
              <Plus className="h-6 w-6 sm:h-8 sm:w-8 text-gray-400" />
            </label>

            {/* Image Previews */}
            {mediaFiles.images.map(image => (
              <div key={image.id} className="relative aspect-square">
                <img 
                  src={image.preview} 
                  alt="Preview" 
                  className="w-full h-full object-cover rounded-lg"
                />
                <button
                  onClick={() => removeMedia('images', image.id)}
                  className="absolute -top-1 -right-1 sm:-top-2 sm:-right-2 bg-red-500 text-white rounded-full p-0.5 sm:p-1 hover:bg-red-600 transition-colors"
                >
                  <X className="w-3 h-3 sm:w-4 sm:h-4" />
                </button>
              </div>
            ))}
          </div>
        </div>

  
        <div className="space-y-2">
          <label className="block text-sm font-medium text-gray-700">Videos</label>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-2 sm:gap-4">
  
            <label className="aspect-square bg-yellow-50 rounded-lg flex flex-col items-center justify-center cursor-pointer hover:bg-yellow-100 transition-colors">
              <input
                type="file"
                accept="video/*"
                onChange={handleVideoUpload}
                className="hidden"
              />
              <div className="flex flex-col items-center gap-1">
                <svg 
                  viewBox="0 0 24 24" 
                  className="w-6 h-6 sm:w-8 sm:h-8 text-gray-400" 
                  fill="none" 
                  stroke="currentColor"
                >
                  <rect x="2" y="2" width="20" height="20" rx="2.18" strokeWidth="2" />
                  <line x1="7" y1="2" x2="7" y2="22" strokeWidth="2" />
                  <line x1="17" y1="2" x2="17" y2="22" strokeWidth="2" />
                  <line x1="2" y1="12" x2="22" y2="12" strokeWidth="2" />
                  <line x1="2" y1="7" x2="7" y2="7" strokeWidth="2" />
                  <line x1="2" y1="17" x2="7" y2="17" strokeWidth="2" />
                  <line x1="17" y1="17" x2="22" y2="17" strokeWidth="2" />
                  <line x1="17" y1="7" x2="22" y2="7" strokeWidth="2" />
                </svg>
                <span className="text-xs sm:text-sm text-gray-600">Add video</span>
              </div>
            </label>

         
            {mediaFiles.videos.map(video => (
              <div key={video.id} className="relative aspect-square">
                <video 
                  src={video.preview} 
                  className="w-full h-full object-cover rounded-lg"
                  controls
                />
                <button
                  onClick={() => removeMedia('videos', video.id)}
                  className="absolute -top-1 -right-1 sm:-top-2 sm:-right-2 bg-red-500 text-white rounded-full p-0.5 sm:p-1 hover:bg-red-600 transition-colors"
                >
                  <X className="w-3 h-3 sm:w-4 sm:h-4" />
                </button>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
  
    const renderStep2 = () => {
      
    
      const handleKeyDown = (e) => {
        if (e.key === 'Enter' && inputValue.trim()) {
          e.preventDefault();
          setAccessories([...accessories, inputValue.trim()]);
          setInputValue('');
        }
      };
    
      const removeAccessory = (indexToRemove) => {
        setAccessories(accessories.filter((_, index) => index !== indexToRemove));
      };
    
      return (
        <div className="space-y-4">
          <h2 className="text-xl font-semibold mb-6">Equipment Information</h2>
          <FormInput
            label="Name of Equipment"
            name="equipmentName"
            register={register}
            error={errors.equipmentName}
          />
          <FormInput
            label="Description"
            name="description"
            register={register}
            error={errors.description}
          />
          <div className="grid grid-cols-2 gap-4">
            <FormInput
              label="Brand"
              name="brand"
              register={register}
              error={errors.brand}
            />
            <FormInput
              label="Model"
              name="model"
              register={register}
              error={errors.model}
            />
          </div>
    
          <div className="flex">

          
          
          <div className="space-y-2  ">
            <label className="block text-sm font-medium text-gray-700">
              Accessories
            </label>
            <div className="flex flex-wrap gap-2 p-2 border rounded-md">
              {accessories.map((accessory, index) => (
                <span
                  key={index}
                  className="inline-flex items-center px-2 py-1 rounded-full text-sm bg-gray-100"
                >
                  {accessory}
                  <button
                    type="button"
                    onClick={() => removeAccessory(index)}
                    className="ml-1 text-gray-500 hover:text-gray-700"
                  >
                    ×
                  </button>
                </span>
              ))}
              <input
                type="text"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                onKeyDown={handleKeyDown}
                placeholder="Type and press Enter to add"
                className="flex-1 outline-none min-w-[200px] p-1"
              />
            </div>
            <p className="text-sm text-gray-500">
              Press Enter to add an accessory
            </p>
          </div>
          <div className=" gap-4 items-center ml-10">
            <p className="text-sm font-medium text-gray-700">Working condition</p>
            <div className="flex gap-4">
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  {...register('condition', { required: 'Please select a condition' })}
                  value="brandNew"
                  className="form-radio h-4 w-4 text-[#2A2F38] focus:ring-[#2A2F38]"
                />
                <span className="ml-2">Brand New</span>
              </label>
              <label className="inline-flex items-center">
                <input
                  type="radio"
                  {...register('condition', { required: 'Please select a condition' })}
                  value="refurbished"
                  className="form-radio h-4 w-4 text-[#2A2F38] focus:ring-[#2A2F38]"
                />
                <span className="ml-2">Refurbished</span>
              </label>
            </div>
          </div>
          {errors.condition && (
            <p className="text-sm text-red-600">{errors.condition.message}</p>
          )}
    
          </div>
        </div>
      );
    };

    const renderStep3 = () => {
      return (
        <div className="space-y-4">
          <h2 className="text-xl text-center font-semibold mb-6">Pricing</h2>
          
          <FormSelect
            label="Price Schedule"
            name="priceSchedule"
            register={register}
            options={[
              { value: 'hourly', label: 'Hourly' },
              { value: 'daily', label: 'Daily' },
              { value: 'weekly', label: 'Weekly' },
            ]}
            error={errors.priceSchedule}
          />
    
          <div className="flex gap-4">
            <FormInput
              label="Price"
              name="price"
              type="number"
              min="0"
              register={register}
              error={errors.price}
            />
            <FormSelect
              label="Currency"
              name="currency"
              register={register}
              options={[
                { value: 'USD', label: 'USD' },
                { value: 'EUR', label: 'EUR' },
              ]}
            />
          </div>
    
          <FormCheckbox
            label="Negotiable"
            name="isNegotiable"
            register={register}
          />
    
          <div className="space-y-2">
            <h3 className="text-sm font-medium text-gray-700">Payment Method</h3>
            <div role="radiogroup" className="space-y-2">
              <FormCheckbox
                label="Pay On Delivery"
                name="paymentMethod"
                value="onDelivery"
                register={register}
                type="radio"
              />
              <FormCheckbox
                label="Payment After Usage"
                name="paymentMethod"
                value="afterUsage"
                register={register}
                type="radio"
              />
            </div>
          </div>
        </div>
      );
      
    };

    const renderStep4 = () => {
      const equipmentData = {
        images: [
          '/path/to/image1.jpg',
          '/path/to/image2.jpg',
          '/path/to/image3.jpg',
        ],
        title: "Steel Pipelines Cables",
        location: "Port Harcourt, Romania",
        cost: 20000000,
        description: "Lorem ipsum dolor sit amet...",
        make: "Port Harcourt, Romania",
        brand: "Port Harcourt, Romania",
        yearOfManufacture: "2023",
        accessories: ["Crane hook", "Crane hook", "Crane hook", "Crane hook", "Crane hook"],
        certificates: [
          "Certificate of use",
          "Certificate of use",
          "Certificate of use",
          "Certificate of use"
        ],
        isUsed: false
      };
      return (
        <ListingSummary {...equipmentData} />
      )
    }
  
    return (
      <div className="max-w-2xl mx-auto p-6 bg-white rounded-lg shadow">
        <h1 className="text-2xl font-bold mb-8 text-center">List an Equipment</h1>
        <form onSubmit={handleSubmit(onSubmit)}>
          {step === 1 && renderStep1()}
          {step === 2 && renderStep2()}
          {step === 3 && renderStep3()}
          {step === 4 && renderStep4()}
          
          <div className="mt-8 flex justify-between">
            {step > 1 && (
              <button
                type="button"
                onClick={() => setStep(step - 1)}
                className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
              >
                Previous
              </button>
            )}
            {step < 4 ? (
              <button
                type="button"
                onClick={() => setStep(step + 1)}
                className="px-4 py-2 text-sm font-medium text-white bg-gray-500 rounded-md hover:bg-gray-800"
              >
                Next
              </button>
            ) : (
              <button
                  type="submit"
                  
                  className={`px-4 py-2 text-sm font-medium text-white rounded-md flex items-center justify-center
                    ${loading ? 'bg-gray-400 cursor-not-allowed' : 'bg-gray-500 hover:bg-gray-800'}`}
                >
                  {loading ? (
                    <>
                      <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                      Submitting...
                    </>
                  ) : (
                    'Complete'
                  )}
                </button>
            )}
          </div>
        </form>
      </div>
    );
  };
  